.android-l {
    background-color: #f6f7f9;
    display: flex;
    flex-direction: row;
    justify-content: center;
    width: 100%;
}

.android-l .div {
    background-color: #f6f7f9;
    height: 800px;
    position: relative;
    width: 360px;
}

.android-l .overlap {
    background-color: #ffffff;
    height: 85px;
    left: 0;
    top: 0;
    position: absolute;
    width: 360px;
}

.android-l .vector {
    height: 16px;
    left: 26px;
    position: absolute;
    width: 9;
    top: 50px;
}

.android-l .text-wrapper {
    color: #111c30;
    font-family: "Poppins-SemiBold", Helvetica;
    font-size: 16px;
    font-weight: 600;
    left: 118px;
    letter-spacing: 0;
    line-height: normal;
    position: absolute;
    text-align: center;
    top: 44px;
}

.android-l .line {
    height: 4px;
    left: 0;
    position: absolute;
    top: 85px;
    width: 245px;
}

